<template>
  <section class="sierpinski">
    <h1>Sierpinski</h1>
    <p>A html canvas drawing a Sierpinski Gasket with the Chaos Game method.</p>
    <canvas
      class="sierpinski-canvas"
      width="300"
      height="300" />

    <div class="container">
      <div class="columns">
        <div class="actions column col-12 my-1">
          <button
            class="btn"
            @click="snapshot()">Snapshot</button>
          <button
            class="btn btn-primary"
            @click="start()">Restart</button>
          <label
            v-if="isHiResAvailable"
            for="hiRes">
            <input
              id="hiRes"
              v-model="hiRes"
              type="checkbox"
              @click="start">
            hi-res
          </label>
        </div>
        <div class="form-group column col-4 col-md-6 my-1">
          <label
            for="pointsPerSecond"
            class="form-label">points/sec</label>
          <input
            id="pointsPerSecond"
            v-model.number="pointsPerSecond"
            class="form-input"
            type="number">
        </div>
      </div>
    </div>

    <div class="snapshots">
      <div
        v-for="snapshot in snapshots"
        :key="snapshot"
        class="snapshot">
        <img
          :src="snapshot"
          :key="snapshot" >
      </div>
    </div>
  </section>
</template>

<script>
/* eslint-disable */
import CanvasWriter from "../assets/canvas-writer";
import canvasColor from "../assets/canvas-color";
import { constantly } from "../assets/constantly";
import { getDevicePixelRatio } from "../assets/get-device-pixel-ratio";
import imageBufferManipulate from "../assets/image-buffer-manipulate";

export default {
  name: "Sierpinski",
  data() {
    return {
      corners: null,
      snapshots: [],
      hiRes: false,
      pointsPerSecond: 1000,
      darkGrey: canvasColor.fromRGB(64, 64, 64)
    };
  },
  mounted() {
    this.start();
  },
  beforeDestroy() {
    this.destroyChildObjects();
  },
  computed: {
    isHiResAvailable() {
      return getDevicePixelRatio() !== 1;
    }
  },
  methods: {
    destroyChildObjects() {
      if (this.canvasWriter) this.canvasWriter.destroy();
      if (this.constantly) this.constantly.destroy();
    },
    start() {
      this.destroyChildObjects();
      const canvas = document.getElementsByClassName("sierpinski-canvas")[0];
      if (typeof jest !== "undefined") return;

      this.canvasWriter = new CanvasWriter(canvas, undefined, this.hiRes);

      this.canvasWriter = Object.assign(
        this.canvasWriter,
        imageBufferManipulate
      );

      this.height = this.canvasWriter.height;
      this.width = this.canvasWriter.width;

      this.canvasWriter.clear(canvasColor.fromRGB(255, 255, 255));
      const scale = this.width;
      this.corners = [];
      const corners = [{ x: 0.5, y: 0 }, { x: 0, y: 1 }, { x: 1, y: 1 }];

      corners.forEach(point =>
        this.corners.push({
          x: point.x * scale,
          y: point.y * scale
        })
      );

      this.currentpoint = {
        x: this.corners[0].x,
        y: this.corners[0].y
      };

      this.constantly = constantly(() => this.addPoint(), this.pointsPerSecond);
    },

    snapshot() {
      this.snapshots.push(this.canvasWriter.toCanvas().toDataURL());
    },
    addPoint() {
      const randomCorner = this.corners[
        ~~(Math.random() * this.corners.length)
      ];

      const point = {
        x: (this.currentpoint.x + randomCorner.x) / 2,
        y: (this.currentpoint.y + randomCorner.y) / 2
      };

      this.canvasWriter.subtractPoint(point.x, point.y, this.darkGrey);

      this.currentpoint = point;
    }
  }
};
</script>

<style lang="scss" scoped>
canvas {
  border: 1px solid shade(0.8);
  width: 500px;
  height: 500px;
  // @media (max-width: $size-md) {
  //   & {
  //     width: 300px;
  //     height: 300px;
  //   }
  // }
}

.snapshots {
  line-height: 0;
  .snapshot {
    display: inline-block;
    width: 24%;
    margin: 0.5%;
    img {
      width: 100%;
    }
  }
}
</style>



